import { useMoralis } from "react-moralis";
import { useMoralisWeb3ApiCall } from "react-moralis";
import { Modal, Button, PageHeader, List } from "antd"
import React, { useState } from 'react';
import Metamask from "./WalletIcons/metamaskWallet.png";
import "./ImgButton.css"

function Account() {
    const { Moralis, isAuthenticated, authenticate, enableWeb3, account, logout } = useMoralis();



    const logInWalletConnect = async () => {
        const user = await Moralis.authenticate({ provider: "walletconnect", chainId: 4 })
        console.log(user)
    }

    //start - modal antd buttons
    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };
    //end - modal antd buttons

    const connectButton = () => {
        console.log("accc", account)
        if (!account) {
            return (<Button key="4" type="primary" onClick={showModal} >
                Connect
            </Button>)
        }
        return ({ account })

    }



    //console.log(isAuthenticated)
    //console.log(!account)
    if (!account) {
        return (
            <>
                <PageHeader
                    className="site-page-header"
                    title="OnChainAsciiApes"
                    extra={[<Button onClick={showModal} >
                        Connect
                    </Button>,
                    <Modal title="Choose wallet" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
                        <input type="submit" id="image-button" value="Metamask" onClick={() => enableWeb3()}></input>
                        <input type="submit" id="image-button-walletconnect" value="WalletConnect" onClick={logInWalletConnect}></input>
                    </Modal>,

                    ]
                    }
                ></PageHeader >

            </>)

    }
    return (
        <>
            <PageHeader style={{ backgroundColor: "white" }}
                className="site-page-header"
                title="OnChainAsciiApes"
                extra={[<Button>
                    {account}
                </Button>,
                <Button onClick={logout}>Logout</Button>,

                ]
                }
            ></PageHeader >

        </>
    )
}
export default Account;